@import "https://unpkg.com/open-props";
* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  align-items: center;
  justify-items: center;
}

button {
  font-family: var(--font-sans);
  font-weight: var(--font-weight-6);
  font-size: var(--font-size-5);
  color: var(--gray-8);
  padding: var(--size-2) var(--size-4);
  cursor: pointer;
  background: #0f0;
  border: 1px solid #000;
}

#datetime-picker {
  margin-bottom: 50px;
  margin-right: 100px;
}

button:active {
  --y: 5%;
  --scale: .9;
  --border-scale: .9, .8;
}

.timer {
  display: flex;
}

.field {
  display: grid;
}

.field:not(:last-child) {
  margin-right: 25px;
}

